<!-- 编辑推荐 歌单页 -->
<template>
	<view class="song">
		<!-- 歌单页头部 -->
		<view class="head">
			<view class="head-left">
				<image class="img" :src="RankingSong.coverImgUrl" mode=""></image>
			</view>
			<view class="head-right">
				<view class="right-top">{{ RankingSong.name }}</view>
				<view class="right-bottom">
					<image class="img6" :src="RankingSong.creator.avatarUrl" mode=""></image>
					<view class="text">{{RankingSong.creator.signature}}</view>
				</view>
			</view>

		</view>

		<view class="song-list"> 歌曲列表 </view>
		<!-- 歌曲列表 -->
		<view class="recommend-text1">
			<view class="newests" v-for="(item,index) in music" :key="item.id" @click="ClickSong(item.id)">

				<view class="newests-left">{{index + 1}}</view>
				<view class="newests-right">
					<view class="authors">{{item.name}}</view>
					<view class="SongTitles">
						<view class="img1" :class="item.sq == null ? 'actives' :''"></view>
						<view class="left">
							<span v-for="(a,b) in item.ar" :key="b">
								{{a.name}}
							</span>
							-
							<span>{{item.name}}</span>
						</view>

					</view>
				</view>

				<view class="img2">
				</view>

			</view>
		</view>

		<view class="jia">
			<view v-if="hide">点击加载更多</view>
			<view v-if="!hide">没有更多了</view>
			<view class="m1" v-if="hide" @click="ClickA(0)"></view>
			<view class="m2" v-if="!hide" @click="ClickA(1)"></view>
		</view>
		<view class="comment">精彩评论</view>
		<view class="comment1">
			{{comment}}
		</view>
		<svg class="u-svg u-svg-unzancmt da" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 28 28">
			<path fill="#999"
				d="m25.857 14.752c-.015.059-1.506 5.867-2.932 8.813-1.162 2.402-3 2.436-3.099 2.436h-12.826v-13c3 0 5.728-4 5.728-7.275 0-3.725 1.433-3.725 2.142-3.725 1.327 0 1.978 1.345 1.978 4 0 2.872-.832 4.525-.839 4.537-.161.31-.155.682.027.981.181.299.5.482.849.482h6.942c.922 0 1.551.215 1.866.64.467.626.286 1.705.164 2.112m-23.857 10.248v-10c0-1.795.659-1.981.855-2h2.145v13h-2.173c-.829 0-.827-.648-.827-1m25.309-13.54c-.713-.969-1.886-1.46-3.482-1.46h-5.519c.26-.932.519-2.285.519-4 0-5.221-2.507-6-4-6-1.909 0-4.185.993-4.185 5.725 0 2.206-1.923 5.275-3.815 5.275h-4-.011c-1.034.011-2.816.862-2.816 4v10.02c0 1.198.675 2.979 2.827 2.979h16.971.035c.364 0 3.224-.113 4.894-3.564 1.514-3.127 3.01-8.942 3.056-9.14.071-.23.664-2.289-.474-3.836">
			</path>
		</svg>
	</view>
</template>

<script>
	import common from '../../common/js/common.js'
	export default {
		data() {
			return {
				id: '',
				RankingSong: '', // 传输的歌单的音乐数据
				music: '', // 音乐长度
				loading: true,
				hide: true,
				hotComments: '' , //热门评论
				comment: '', // 评论
			}
		},
		methods: {
			// 歌单请求 https://apis.netstart.cn/music/comment/playlist?id=
			RequestSongs() {
				uni.request({
					url: `https://apis.netstart.cn/music/playlist/detail?id=${this.id}`,
					method: 'GET',
					header: {
						'content-type': 'application/json'
					},
					success: data => {
						console.log('歌单页请求', data.data.playlist)
						this.RankingSong = data.data.playlist
						this.music = this.RankingSong.tracks.slice(0, 10);
						console.log(this.RankingSong);
					}
				})
			},
			// 获取评论
			RequestSong() {
				uni.request({
					// url: `https://apis.netstart.cn/music/comment/playlist?id=${this.id}`,
					url: `https://apis.netstart.cn/music/comment/playlist?id=${this.id}`,
					method: 'GET',
					header: {
						'content-type': 'application/json'
					},
					success: data => {
						console.log('评论请求', data)
						this.comment = data
						// this.music= this.RankingSong.tracks.slice(0,10);
						// console.log(this.RankingSong);
					}
				})
			},
			ClickA(el) {
				if (el == 0) {
					this.hide = false
					this.music = this.RankingSong.tracks.slice(0, this.RankingSong.length);
				} else {
					this.hide = true
					this.music = this.RankingSong.tracks.slice(0, 10);
				}
			}
		},

		beforeMount() {
			this.RequestSongs();
			this.RequestSong();
		},
		onLoad: function(option) { //option为object类型，会序列化上个页面传递的参数
			console.log('歌曲id',option.id); //打印出上个页面传递的参数。
			this.id = option.id

		}
	}
</script>

<style lang="scss" src="./SongPage.scss">

</style>
